.grid {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 -20px;
  overflow: hidden;
}
.grid-5 .grid-item {
  width: calc((100% - 2 * 20px * 5 ) / 5);
}
.grid-3 .grid-item {
  width: calc((100% - 2 * 20px * 3 ) / 3);
}
.grid-2 {
  margin: 0 -10px;
}
.grid-2 .grid-item {
  width: calc((100% - 2 * 10px * 2 ) / 2);
  margin-right: 10px;
  margin-left: 10px;
}

.grid-item {
  position: relative;
  margin-right: 20px;
  margin-left: 20px;
  margin-bottom: 3.5rem;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  .grid-5 .grid-item {
    width: calc((100% - 2 * 20px * 3 ) / 3);
  }
}
@media screen and (max-width: 768px) {
  .grid-5 .grid-item, .grid-3 .grid-item {
    width: calc((100% - 2 * 20px * 2 ) / 2);
  }

  .grid-2 .grid-item {
    width: calc((100% - 2 * 10px * 1 ) / 1);
  }
}
@media screen and (max-width: 480px) {
  .grid-5 .grid-item, .grid-3 .grid-item {
    width: calc((100% - 2 * 20px * 1 ) / 1);
  }
}